import React, { useContext, useEffect, useState } from "react";
import "../archive/archive.scss";
import CardItem from "src/components/CardItem";
import axios from "axios";
import { Loader } from "src/components/Loader";
import { UserContext } from "src/App";

function GalleryYearView() {
  let curr_url = window.location.pathname.split("/");
  let curr_year = curr_url[2];

  const setLoading = useContext(UserContext);
  const [folders, setFolders] = useState([]);

  useEffect(() => {
    const loadYear = async () => {
      setLoading(true);
      await axios.get("https://juwenaliapw-api.westeurope.cloudapp.azure.com/api/images/folders/", {
        params: {
          year: curr_year,
        },
      }).then(function (response) {
        setFolders(response.data["folders"]);
        setLoading(false);
      })
        .catch(function (error) {
          if (error.response) {
            console.log(error.response.data);
            console.log(error.response.status);
            console.log(error.response.headers);
          } else {
            console.log(error.request);
          }
          setLoading(false);
        });
    };
    loadYear();

  }, []);

  return (
    <>
      <div className="archive-folders">
        <div className="archive-folders" style={{ minHeight: "83vh" }}>
          <div className="col-10 mx-auto">
            <h1>Juwenalia PW {curr_year}</h1>
            <div className="cards">
              <div className="cards__container">
                <ul className="cards__items">
                  {folders.map((folder: string, i) =>
                  (
                    <CardItem
                      src={"http://juwenaliapw-api.westeurope.cloudapp.azure.com/mediafiles/public/images".concat('/', curr_year).concat('/', folder).concat('/', 'pic1.jpg')}
                      text={folder.replaceAll('_', ' ')}
                      path={folder}
                      key={i}
                    />
                  )
                  )}
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div >
    </>
  );
}

export default GalleryYearView;
