import React, { Dispatch, SetStateAction, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import "./MessageModal.scss"

interface MessageModalProps {
    status: number;
    show: boolean;
    setShow: Dispatch<SetStateAction<boolean>>;
}

function MessageModal(props: MessageModalProps) {
    let modalProporties = {
        title: "",
        body: "",
    }
    if (props.status === 201) {
        modalProporties.title = "Sukces";
        modalProporties.body = "Udao si wysa wiadomo!"
    }
    else if (props.status === 413) {
        modalProporties.title = "Slowchat jest wczony";
        modalProporties.body = "Poczekaj przed wysaniem nastpnej wiadomoci."
    }
    else {
        modalProporties.title = "Wystpi bd";
        modalProporties.body = "Nie udao si wysa wiadomoci. Sprbuj ponownie pniej."
    }

    let status = props.status;
    let show = props.show;
    let setShow = props.setShow;

    return (
        <Modal show={show} onHide={() => setShow(false)} centered>
            <Modal.Header closeButton>
                <Modal.Title>{modalProporties.title}</Modal.Title>
            </Modal.Header>
            <Modal.Body>{modalProporties.body}</Modal.Body>
            <Modal.Footer>
                <Button className="modal-button" onClick={() => setShow(false)}>
                    Zamknij
                </Button>
            </Modal.Footer>
        </Modal>
    );
}

export default MessageModal;