import React from "react";
import "../../App.scss";
import HomepageVideo from "../home/counter";
import LineupCards from "../lineup/LineupCards";
import Partners from "../partners/partners";
import FAQ from "../FAQ/FAQ";
import GalleryCards from "../archive/archiveCards";
import JakDojade from "../jakdojade/jakDojade";
import Juweteam from "../juweteam/juweteam";
import MobileMessageToast from "src/components/mobileMessageToast/MobileMessageToast";

function Home() {
  return (
    <>
      <section id="top">
        <HomepageVideo />
      </section>
      <section id="lineup">
        <LineupCards />
      </section>
      {window.innerWidth > 960 ?
        <>
          <section id="archive">
            <GalleryCards />
          </section>
        </>
        :
        <>
          <section id="message">
            <MobileMessageToast />
          </section>
        </>
      }
      <section id="juweteam">
        <Juweteam />
      </section>
      <section id="partners">
        <Partners />
        {window.innerWidth > 960 && <JakDojade />}
      </section>
      <section id="faq">
        <FAQ />
      </section>
    </>
  );
}

export default Home;
