import React, { useContext, useEffect, useState } from "react";
import "../archive/archive.scss";
import ImageGallery from "react-image-gallery";
import "react-image-gallery/styles/scss/image-gallery.scss";
import axios from "axios";
import { Loader } from "src/components/Loader";
import { UserContext } from "src/App";

function GalleryFolderView() {
  let curr_url = window.location.pathname.split("/");
  let curr_year = curr_url[2];
  let curr_folder = curr_url[3];

  // const [loading, setLoading] = useState(false);
  const setLoading = useContext(UserContext);
  const [images, setImages] = useState([]);

  useEffect(() => {
    const loadFolder = async () => {
      setLoading(true);
      await axios
        .get("https://juwenaliapw-api.westeurope.cloudapp.azure.com/api/images/gallery/", {
          params: {
            year: curr_year,
            folder: curr_folder,
          },
        })
        .then(function (response) {
          setImages(response.data["photos"]);
          setLoading(false);
        })
        .catch(function (error) {
          if (error.response) {
            console.log(error.response.data);
            console.log(error.response.status);
            console.log(error.response.headers);
          } else {
            console.log(error.request);
          }
          setLoading(false);
        });
    };

    loadFolder();
  }, []);

  return (
    <>
      <div className="imageGallery">
        {/* {loading ? (
          <Loader style={{ height: "86vh" }} />
        ) : ( */}
        <div className="gallery-wraper" style={{ height: "100%", minHeight: "86vh" }}>
          <ImageGallery
            showPlayButton={false}
            lazyLoad={true}
            items={images}
          />
        </div>
        {/* )} */}
      </div>
    </>
  );
}

export default GalleryFolderView;
