import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "./Navbar.scss";
import { HashLink } from "react-router-hash-link";
import { Logo } from "./Logo";

function Navbar() {
  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);

  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);

  const showButton = () => {
    if (window.innerWidth <= 1000) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    showButton();
  }, []);

  window.addEventListener("resize", showButton);

  return (
    <>
      <nav className="navbar-site">
        <div className="navbar-container">
          <HashLink to="/#" onClick={closeMobileMenu} className="navbar-logo">
            <Logo />
          </HashLink>
          <div className="menu-icon" onClick={handleClick}>
            <i className={click ? "fas fa-times" : "fas fa-bars"} />
          </div>
          <div className={click ? "nav-menu active" : "nav-menu"}>
            <div className="nav-item">
              <Link
                to="https://ticketclub.pl/koncerty/varsonalia-pw-2023-dzien-i-stadion-syrenka-warszawa-2208/"
                className="nav-links tickets"
                onClick={closeMobileMenu}
                target="_blank"
                rel="noopener noreferrer"
              >
                KUP BILET
              </Link>
            </div>
            {window.innerWidth > 960 ? (
              <>
                <div className="nav-item">
                  <HashLink
                    to="/#lineup"
                    smooth
                    className="nav-links"
                    onClick={closeMobileMenu}
                  >
                    LINE-UP
                  </HashLink>
                </div>
                <div className="nav-item">
                  <HashLink
                    to="/#archive"
                    smooth
                    className="nav-links"
                    onClick={closeMobileMenu}
                  >
                    GALERIA
                  </HashLink>
                </div>
              </>
            ) : (
              <div className="nav-item">
                <HashLink
                  to="/#message"
                  smooth
                  className="nav-links"
                  onClick={closeMobileMenu}
                >
                  WYLIJ WIADOMO NA TELEBIM
                </HashLink>
              </div>
            )}
            <div className="nav-item">
              <HashLink
                to="/#juweteam"
                smooth
                className="nav-links"
                onClick={closeMobileMenu}
              >
                JUWETEAM
              </HashLink>
            </div>
            <div className="nav-item">
              <HashLink
                to="/#faq"
                smooth
                className="nav-links"
                onClick={closeMobileMenu}
              >
                FAQ
              </HashLink>
            </div>
          </div>
        </div>
      </nav>
    </>
  );
}

export default Navbar;
