
import { useContext, useState } from 'react';
import Toast from 'react-bootstrap/Toast';
import Button from 'react-bootstrap/esm/Button';
import Col from 'react-bootstrap/esm/Col';
import Form from 'react-bootstrap/esm/Form';
import Row from 'react-bootstrap/esm/Row';
import ToastContainer from 'react-bootstrap/esm/ToastContainer';
import "./MessageToast.scss"
import MessageModal from './MessageModal';
import axios from 'axios';
import { UserContext } from 'src/App';

function MessageToast() {

    const [minimised, setMinimised] = useState<boolean>(true);
    const [showModal, setShowModal] = useState(false);
    const [author, setAuthor] = useState("");
    const [message, setMessage] = useState("");
    const [status, setStatus] = useState<number>(0);
    const [validated, setValidated] = useState(false);
    const setLoading = useContext(UserContext);

    function axiosPost() {
        setLoading(true);
        axios.post(`http://juwenaliapw-api.westeurope.cloudapp.azure.com/api/messages/`, { author: author, message: message, status: null })
            .then(res => {
                setStatus(res.status);
                setMinimised(!minimised);
                setAuthor("");
                setMessage("");
                setValidated(false);
                setLoading(false);
                setShowModal(true);
            })
            .catch(err => {
                setStatus(err.response.status);;
                setValidated(false);
                setLoading(false);
                setShowModal(true);
            })
    }

    function handleSubmit(e: any) {
        setValidated(true);
        e.preventDefault();
        if (author && message) {
            axiosPost();
        }
    }

    return (

        <div className={minimised ? "message-toast fade-out" : "message-toast"}>
            <MessageModal show={showModal} setShow={setShowModal} status={status} />
            <ToastContainer className="px-5" >
                <Toast onClose={() => setMinimised(!minimised)} style={{ width: "30rem" }}>
                    <Toast.Header closeButton={false} onClick={() => setMinimised(!minimised)} style={{ cursor: "pointer" }}>
                        {/* <img src="holder.js/20x20?text=%20" className="rounded me-2" alt="" /> */}
                        <span style={{ fontSize: "1.1rem" }} className="me-auto">Wylij wiadomo na telebim</span>
                        {/* <small>11 mins ago</small> */}
                    </Toast.Header>
                    <Toast.Body>
                        <Form noValidate validated={validated} onSubmit={handleSubmit}>
                            <Row>
                                <Col>
                                    <span className="caption">Autor <small>(max 15 znakw)</small></span>
                                    <Form.Control
                                        required
                                        maxLength={15}
                                        name="Order ID"
                                        type="text"
                                        value={author}
                                        onChange={(e: any) => { setAuthor(e.target.value) }}
                                    />
                                </Col>
                            </Row>
                            <Row className="py-3">
                                <Col>
                                    <span className="caption">Wiadomo <small>(max 100 znakw)</small></span>
                                    <Form.Control
                                        required
                                        maxLength={100}
                                        as="textarea"
                                        style={{ resize: 'none', overflow: 'hidden', height: '15rem' }}
                                        name="adminNotes"
                                        // disabled={disable}
                                        value={message}
                                        onChange={(e: any) => { setMessage(e.target.value) }}
                                    />
                                </Col>
                            </Row>
                            <Button
                                type="submit"
                                className="toast-button"
                            // disabled={disable}
                            // onClick={(e: any) => {
                            //     e.preventDefault();
                            //     // onSubmit();
                            // }}
                            >
                                Wylij!
                            </Button>
                        </Form>
                    </Toast.Body>
                </Toast>
            </ToastContainer>
        </div >

    );
}

export default MessageToast;