import React, { createContext, useState } from "react";
import Navbar from "./components/Navbar";
import "./App.scss";
import { BrowserRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import Home from "./pages/home/home";
import GalleryFolderView from "./pages/archive/archiveFolder";
import GalleryYearView from "./pages/archive/archiveYear";
import "bootstrap/dist/css/bootstrap.min.css";
import Footer from "./components/Footer";
import MessageToast from "./components/messageToast/MessageToast";
import "react-alice-carousel/lib/scss/alice-carousel.scss";
import { useWindowSize } from "./Hooks/useWindowSize";
import Container from "react-bootstrap/esm/Container";
import NotFound from "./pages/404/404";
import { Loader } from "./components/Loader";

export const UserContext = createContext<React.Dispatch<React.SetStateAction<boolean>> | Function>(() => { });

function App() {
  const [width, height] = useWindowSize();
  const [loading, setLoading] = useState(false);


  return (
    <Container fluid="true">
      <Router>
        <UserContext.Provider value={setLoading}>
          {loading && <Loader />}
          <Navbar />
          {window.innerWidth > 960 &&
            <MessageToast />
          }
          <Routes>
            <Route path="/" Component={Home} />
            <Route path="/archive/:year" Component={GalleryYearView} />
            <Route path="/archive/:year/:folder" Component={GalleryFolderView} />
            <Route path='/404' Component={NotFound} />
            <Route
              path="*"
              element={<Navigate to="/404" replace />}
            />
          </Routes>
          <Footer />
        </UserContext.Provider>
      </Router>
    </Container>
  );
}

export default App;
