import React from "react";
import "../archive/archiveCards.scss";
import CardItem from "src/components/CardItem";

function GalleryCards() {
  return (
    <>
      <div className="archive-cards">
        <div className="col-10 mx-auto">
          <h1>Galeria zdj</h1>
          <div className="cards">
            <div className="cards__container">
              <ul className="cards__items">
                <CardItem
                  src="/images/galeria_head/2022.jpg"
                  text="Juwenalia PW 2022"
                  path="/archive/2022"
                />
                <CardItem
                  src="/images/galeria_head/2019.jpg"
                  text="Juwenalia PW 2019"
                  path="/archive/2019"
                />
                <CardItem
                  src="/images/galeria_head/2018.jpg"
                  text="Juwenalia PW 2018"
                  path="/archive/2018"
                />
                <CardItem
                  src="/images/galeria_head/2017.jpg"
                  text="Juwenalia PW 2017"
                  path="/archive/2017"
                />
                <CardItem
                  src="/images/galeria_head/2016.jpg"
                  text="Juwenalia PW 2016"
                  path="/archive/2016"
                />
                <CardItem
                  src="/images/galeria_head/2015.jpg"
                  text="Juwenalia PW 2015"
                  path="/archive/2015"
                />
              </ul>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default GalleryCards;
